<template>
    <div>
        <section id="dashboard-map">
            <div class="container">
                <div class="row">
                    <div class="col-lg-3 col-md-6 col-sm-12">
                        <div class="overview">
                            <div class="stat-icon icon-color-one">
                                <i class="fas fa-chart-pie"></i>
                            </div>
                            <div class="stat-text">
                                <p>Total Services</p>
                                <span>{{license.total}}</span>
                            </div>
                        </div>
                    </div>
                    <div class="col-lg-3 col-md-6 col-sm-12">
                        <div class="overview">
                            <div class="stat-icon icon-color-two">
                                <i class="fas fa-chart-line"></i>
                            </div>
                            <div class="stat-text">
                                <p>Total Transactions</p>
                                <span>{{transactions.total}}</span>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="row">
                    <div class="col-sm-12 col-md-12 col-lg-12">
                        <div class="milestone-wrapper">
                            <div class="milestone">
                                <h3>Purchased Services</h3>
                                <a class="float add-milestone" href="#">Purchase More</a>
                            </div>
                        </div>
                        <div class="db-table table-responsive">
                            <span v-if="license.data.length < 1 || license.data === null || license.data === undefined" style="text-align: center;width: 100%;padding: 30px;font-weight: bold;font-size: 20px">
                                No Services Purchased
                            </span>
                            <table class="table" v-else>
                                <thead>
                                <tr>
                                    <th>Services ID</th>
                                    <th>Services Description</th>
                                    <th>Services Amount</th>
                                    <th>Services E-Pin</th>
                                    <th>Services E-Policy</th>
                                    <th>Services Date</th>
                                </tr>
                                </thead>
                                <tbody v-for="(licence, index) in license.data" :key="index">
                                <tr>
                                    <td>#{{licence.transaction_id}}</td>
                                    <td>${{licence.transaction_amount}}</td>
                                    <td>{{licence.transaction_description}}</td>
                                    <td>{{licence.e_pin_text}}</td>
                                    <td>{{licence.e_pin_policy}}</td>
                                    <td>{{ $dateYear(licence.transaction_date,{timestamp: true})}}</td>
                                </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
                <br>
                <div class="row">
                    <div class="col-sm-12 col-md-12 col-lg-12">
                        <div class="milestone-wrapper">
                            <div class="milestone">
                                <h3>Recent Transactions</h3>
                            </div>
                        </div>
                        <div class="db-table table-responsive">
                            <span v-if="transactions.data.length < 1 || transactions.data === null || transactions.data === undefined" style="text-align: center;width: 100%;padding: 30px;font-weight: bold;font-size: 20px">
                                No transactions made
                            </span>
                            <table class="table" v-else>
                                <thead>
                                <tr>
                                    <th>Transaction ID</th>
                                    <th>Transaction Amount</th>
                                    <th>Transaction Description</th>
                                    <th>Transaction Reference</th>
                                    <th>Transaction Date</th>
<!--                                    <th>Action</th>-->
                                </tr>
                                </thead>
                                <tbody v-for="(transaction, index) in transactions.data" :key="index">
                                <tr>
                                    <td>#{{transaction.transaction_id}}</td>
                                    <td>${{transaction.transaction_amount}}</td>
                                    <td>{{transaction.transaction_description}}</td>
                                    <td>{{transaction.transaction_reference}}</td>
                                    <td>{{ $dateYear(transaction.transaction_date,{timestamp: true})}}</td>
                                </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </div>
        </section>

    </div>
</template>

<script>
    export default {
        name: "index",
        data() {
            return {
                transactions: {
                    data: [],
                    first_page_url: '',
                    next_page_url: '',
                    last_page_url: '',
                    last_page: '',
                    prev_page_url: '',
                    total: '',
                },
                license: {
                    data: [],
                    first_page_url: '',
                    next_page_url: '',
                    last_page_url: '',
                    last_page: '',
                    prev_page_url: '',
                    total: '',
                },
            }
        },
        mounted() {
            this.getAllTransactions()
            this.getAllLicence()
        },
        methods: {
            timeAgoFunction(timestamp) {
                return this.$timeAgo.format(new Date(timestamp * 1000));
            },
            getAllTransactions() {
                this.axios.get('/transactions/10')
                .then(res => {
                    // console.log(res.data.data.transactions)
                    const transactions = res.data.data.transactions;
                    this.transactions.first_page_url = transactions.first_page_url;
                    this.transactions.last_page_url = transactions.last_page_url;
                    this.transactions.last_page = transactions.last_page;
                    this.transactions.next_page_url = transactions.next_page_url;
                    this.transactions.prev_page_url = transactions.prev_page_url;
                    this.transactions.total = parseInt(transactions.total, 10);
                    this.transactions.data = transactions.data;
                    this.$errors('Success!', res.data.data, res.data.message, res.status)
                })
                .catch(err => {
                    // console.log(err.response.status)
                    this.$errors('Error!', err.response.data.message, err.message,err.response.status)
                })
            },
            getAllLicence() {
                this.axios.get('/licence/10')
                .then(res => {
                    const license = res.data.data.licences;
                    // console.log(license)
                    this.license.first_page_url = license.first_page_url;
                    this.license.last_page_url = license.last_page_url;
                    this.license.last_page = license.last_page;
                    this.license.next_page_url = license.next_page_url;
                    this.license.prev_page_url = license.prev_page_url;
                    this.license.total = parseInt(license.total, 10);
                    this.license.data = license.data;
                    this.$errors('Success!', res.data.data, res.data.message, res.status)
                })
                .catch(err => {
                    this.$errors('Error!', err.response.data.message, err.message,err.response.status)
                })
            },
        },
    }
</script>

<style scoped>

